import React, { useState } from 'react'
import './Login.css'
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

import username_icon from '../Assets/username.png'
import password_icon from '../Assets/password.png'
import background_img from '../Assets/background.jpg'
import passwordEyeOpen_icon from '../Assets/showPassword.png'
import passwordEyeClosed_icon from '../Assets/noPassword.png'
import logoBackground from '../Assets/logo.jpeg'

const apiLogin = 'http://192.168.1.113:3000/api/users/login'

export const Login = () => {
    const navigate = useNavigate();
    const [email, setEmail] = useState('');
    const [contrasea, setPassword] = useState('');
    const [errorM, setError] = useState('');
    const [passwordVisible, setPasswordVisible] = useState(false);
    const emailRegex = /^[a-zA-Z0-9._%+-]+@(hotmail|gmail|yahoo|outlook)\.com$/;


    const navigateToMenu = () => {
        navigate("/pagina-inicio");
    };
    const testHandleSubmitAdmin = () => {
        navigate("/admin-home");
    }

    const isLoginDisabled = email.trim() === '' || contrasea.trim() === '';
    const isShowPasswordDisabled = contrasea.trim() === '';

    const validateEmail = () => {
        if (!emailRegex.test(email)) {
            // If email format is incorrect, add 'error' class to the email input container
            document.getElementById('emailInput').classList.add('error');
        } else {
            // If email format is correct, remove 'error' class from the email input container
            document.getElementById('emailInput').classList.remove('error');
        }
    };

    const tesHandleSubmit = () => {
        navigateToMenu();
    }

    const handleSubmit = async (e) => {
        validateEmail();
        e.preventDefault();
        try {
            const response = await axios.post(apiLogin, {
                email,
                contrasea
            });
            console.log(response.data);
            navigateToMenu();
            // Manejar la respuesta del servidor segn sea necesario
        } catch (errorM) {
            console.error('Error al iniciar sesin:', errorM);
            setError('Correo electrnico o contrasea incorrecto/s.');
            setPassword('');
        }
    };

    const togglePasswordVis = () => {
        setPasswordVisible(!passwordVisible)
    }

    return (
        <div className="fullpage">
            <div className='container'>
                <div className="background">
                    <img src={background_img} alt="" />
                </div>
                <div className="header">
                    {/*<div className="text">Inicio de Sesin</div>
                    <div className="underline"></div>*/}
                    <div className="logo">
                        <img src={logoBackground} alt="" />
                    </div>
                </div>
                <div className="login-text">Iniciar sesin con su correo institucional.</div>
                <div className="inputs">
                    <div className="input">
                        <div className={`input ${email && !emailRegex.test(email) ? 'error' : ''}`} id="emailInput">
                            <img src={username_icon} alt="" />
                            <input type="email" placeholder='Correo Electrnico' value={email} onChange={(e) => setEmail(e.target.value)} />
                        </div>
                    </div>
                    <div className="input">
                        <img src={password_icon} alt="" />
                        <input type={passwordVisible ? "text" : "password"} placeholder='Contrasea' value={contrasea} onChange={(e) => setPassword(e.target.value)} />
                        <div className="show-password">
                            <button onClick={togglePasswordVis} disabled={isShowPasswordDisabled}><img src={passwordVisible ? passwordEyeOpen_icon : passwordEyeClosed_icon} alt="" /></button>
                        </div>
                    </div>
                </div>
                {/*<div className="forgot-password">Olvidaste tu contrasea? <span>Presiona aqu!</span></div>*/}
                <div className="error-message"> {errorM && <div>{errorM}</div>} </div>
                <div className="submit-container"><button onClick={tesHandleSubmit} disabled={isLoginDisabled}>Iniciar Sesin</button></div>
                <div className="submit-container2"><button onClick={testHandleSubmitAdmin}>Admin Home</button></div>
                {/*<div className="create-account">Todava no tienes una cuenta? <span>Crear cuenta</span></div>*/}
            </div>
        </div>
    )
}